import * as yup from 'yup';

export const buyTrue = yup.object({
    name: yup.string().min(2, "'    4 ").matches(/^[a-zA-Z--' ]+$/, '    ').required("   '"),
    surname: yup.string().min(4, "    4 ").matches(/^[a-zA-Z--' ]+$/, '    ').required("   "),
    age: yup.number().min(18, '  (   18  )').required("   "),
    address: yup.string().min(8).required("   "),
    phone: yup.string().min(8).matches(/^\D?(\d{3})\D?\D?(\d{3})\D?(\d{2})\D?(\d{2})$/, " ,    ").required(" ,   "),
})