import React from 'react';
import {Formik} from 'formik';
import TextField from '@mui/material/TextField';
import Box from "@mui/material/Box";
import {useStyleForm} from "./styles/stylesForm";
import Button from "@mui/material/Button";
import {buyTrue} from "./buyTrue";
import {useDispatch, useSelector} from "react-redux";
import {closeForm, removeProduct, submitFormTrue} from "../../store/reducers";
import { PatternFormat } from 'react-number-format';

const initialValues = {
    name: "",
    surname: "",
    age: "",
    address: "",
    phone: "",
};

const FormBuy = () => {
    const classes = useStyleForm();
    const dispatch = useDispatch();
    const cart = useSelector(state => state.product.cart);
    const products = useSelector(state => state.product.products);

    const handleIsSubmitForm = async (e) => {
        e.preventDefault();

        try {
            const formData = new FormData(e.target);

            const formObject = {};
            formData.forEach((value, key) => {
                formObject[key] = value;
            });

            await buyTrue.validateSync(formObject, {abortEarly: false}); //   

            await dispatch(submitFormTrue()); //

            const purchasedProductsMap = cart.reduce((acc, productId) => {
                const product = products.find((p) => p.id === productId);
                if (product) {
                    if (acc[productId]) {
                        acc[productId].count += 1;
                        acc[productId].totalPrice += +(product.price.replace(/\s/g, '')); //      
                    } else {
                        acc[productId] = {
                            id: product.id,
                            name: product.name, //  
                            totalPrice: +(product.price.replace(/\s/g, '')), //   
                            count: 1, //   
                        };
                    }
                }
                return acc;
            }, {}); //   

            const purchasedProducts = Object.values(purchasedProductsMap); // 

            console.log("  :", formObject); //  
            console.log(" :", purchasedProducts); //   

            cart.forEach((productId) => {
                dispatch(removeProduct({id: productId}));
            });


        } catch (error) {
            console.error("Error submitting form:", error);
        }
    };

    const handleIsCloseForm = () => {
        dispatch(closeForm());
    };
    const handleFormClick = (e) => {
        e.stopPropagation();
    };
    return (
        <div className={classes.formWrapper} onClick={handleIsCloseForm}>
            <Formik initialValues={initialValues} validationSchema={buyTrue}>
                {({errors, touched, handleChange, handleBlur}) => (
                    <Box component="form"
                         sx={{
                             '& .MuiTextField-root': {m: 1, width: '98%'},
                         }}
                         onClick={handleFormClick}
                         className={classes.formComponent}
                         onSubmit={handleIsSubmitForm}
                    >
                        <TextField
                            required
                            id="name"
                            name="name"
                            type="text"
                            label="'"
                            placeholder="  '"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            error={touched.name && Boolean(errors.name)}
                            helperText={touched.name && errors.name}
                        />

                        <TextField
                            required
                            id="surname"
                            name="surname"
                            type="text"
                            label=""
                            placeholder="  "
                            onChange={handleChange}
                            onBlur={handleBlur}
                            error={touched.surname && Boolean(errors.surname)}
                            helperText={touched.surname && errors.surname}
                        />

                        <TextField
                            required
                            id="age"
                            name="age"
                            type="number"
                            label=""
                            placeholder="  "
                            onChange={handleChange}
                            onBlur={handleBlur}
                            error={touched.age && Boolean(errors.age)}
                            helperText={touched.age && errors.age}
                        />

                        <TextField
                            required
                            id="address"
                            name="address"
                            type="text"
                            label=" "
                            placeholder=" : , . ."
                            onChange={handleChange}
                            onBlur={handleBlur}
                            error={touched.address && Boolean(errors.address)}
                            helperText={touched.address && errors.address}
                        />

                        <PatternFormat
                            format="+38(###)###-##-##"
                            mask="_"
                            placeholder="+38(123)456-78-90"
                            required
                            id="phone"
                            name="phone"
                            type="text"
                            label=" "
                            customInput={TextField}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            error={touched.phone && Boolean(errors.phone)}
                            helperText={touched.phone && errors.phone}
                        />
                        <Button variant="contained" color="success" type="submit"></Button>
                    </Box>
                )}
            </Formik>
        </div>
    );
}

export default FormBuy;